import React, {useEffect} from "react";
import './final-order.css';
import {useDispatch, useSelector} from "react-redux";
import {
  billSelector, decreasingIngredients,
  gettingFinalPrice, increasingIngredients, ingredientsSelector,
  noodleSelector, sauceSelector, increasingSauce, decreasingSauce
} from "../../redux/slices/noodle-slice";


export const FinalOrder = ({}) => {
  const noodle = useSelector(noodleSelector)
  const ingredients = useSelector(ingredientsSelector)
  const sauce = useSelector(sauceSelector)

  const price = useSelector(billSelector)
  const dispatch = useDispatch()

  useEffect(() => {                         // Обновление итоговой стоимости
    dispatch(gettingFinalPrice())
    console.log(ingredients)
  })


  const increasingIngredient = (id) => {              // Добавить 1 начинку
    const payload = {
      id: id
    }
    dispatch(increasingIngredients(payload))
  }

  const decreasingIngredient = (id) => {                    // Отнять 1 начинку
    const payload = {
      id: id
    }
    dispatch(decreasingIngredients(payload))

  }

  const increasingSauceCount = (id) => {              // Добавить 1 sauce
    const payload = {
      id: id
    }
    dispatch(increasingSauce(payload))
  }

  const decreasingSauceCount = (id) => {                    // Отнять 1 sauce
    const payload = {
      id: id
    }
    dispatch(decreasingSauce(payload))

  }


    return (
        <div className={'final-order'}>
          <h1>Ваш вок по составу:</h1>
          <div>
            {
              (noodle !== null) && (
                <div className={'order-item'}>
                  - {noodle.name}
                </div>
              )
            }

            <div>
              {
                (ingredients !== null) && (
                  ingredients.map(i => (
                    <div className={'order-item-container'}>
                      <div className={'order-item'}>
                        - {i.name}
                      </div>
                      <div className={'order-item'}>
                        <button onClick={() => decreasingIngredient(i.id)}>-</button>
                        {i.count}
                        <button onClick={() => increasingIngredient(i.id) }>+</button>
                      </div>
                    </div>

                  ))
                )
              }
            </div>

            <div>
              {
                (sauce !== null) && (
                  sauce.map(i => (
                    <div className={'order-item-container'}>
                      <div className={'order-item'}>
                        - {i.name}
                      </div>
                      <div className={'order-item'}>
                        <button onClick={() => decreasingSauceCount(i.id)}>-</button>
                        {i.count}
                        <button onClick={() => increasingSauceCount(i.id) }>+</button>
                      </div>
                    </div>
                  )))
              }
            </div>
          </div>

          <div>
            <em>
              <b>
                {
                  price !== 0 ?  ('Bill: ' + price) : null
                }
              </b>
            </em>
          </div>
        </div>
    )
}