import React, {useEffect, useState} from "react";
import './sauce-item.css';
import {useDispatch, useSelector} from "react-redux";
import {choosingSauce, decreasingSauce, increasingSauce, sauceSelector} from "../../redux/slices/noodle-slice";


export const SauceItem = ({id, name, price, mass}) => {

    const dispatch = useDispatch()

    const increasing = () => {
        const payload = {
            id: id
        }
        dispatch(increasingSauce(payload))
    }

    const decreasing = () => {
        const payload = {
            id: id
        }
        dispatch(decreasingSauce(payload))
    }

    const [amount, setAmount] = useState(0)
    const sauces = useSelector(sauceSelector)

    useEffect(() => {
        const sauce = sauces.find(i => i.id === id)

        if (sauce === undefined) {                         // убирает счетчик, когда он уменьшается до нуля
            setIsClicked(false)
            return
        }

        setAmount(sauce.count)

    }, [sauces])


                                        // появление кнопок при клике на айтем и выбор соуса;
    const [isClicked, setIsClicked] = useState(false)

    const clicking = () => {
        setIsClicked(!isClicked)
        const payload = {
            id: id
        }

        dispatch(choosingSauce(payload))
    }

    return (
        <div className={'sauce-item'}>

            <div
              onClick={clicking}
            >
                <div>{name}</div>
                <div>{price} ₽ | {mass} гр.</div>
            </div>
            {isClicked === false ? null : (
              <div>
                  <button onClick={decreasing}>-</button>
                  {amount}
                  <button onClick={increasing}>+</button>
              </div> )
            }

        </div>
    )
}