import React from "react";
import './sauce.css';
import {SauceItem} from "./sauce-item";
import {SauceList} from "../wok-data";


export const Sauce = ({}) => {

    return (
        <div className={'sauce'}>
            <h1>Выберите соус</h1>
            <div className={'sauce-body'}>
                {
                    SauceList.map(i => (
                      <div
                        className={'sauce-item-container'}
                      >
                          <SauceItem
                            id={i.id}
                            name={i.name}
                            price={i.price}
                            mass={i.mass}
                          />
                      </div>))
                }
            </div>
        </div>
    )
}