import React, {useEffect, useState} from "react";
import './ingredients-item.css';
import {useDispatch, useSelector} from "react-redux";
import {
  choosingIngredients,
  decreasingIngredients,
  increasingIngredients,
  ingredientsSelector,
} from "../../redux/slices/noodle-slice";


export const IngredientsItem = ({id, name, price, mass, description}) => {

  const dispatch = useDispatch()


  const increasing = (id) => {                                  // Increasing
    const payload = {
      id: id
    }
    dispatch(increasingIngredients(payload))
    console.log('Amount incr is' + amount)
  }

  const decreasing = (id) => {                                    // Decreasing
    const payload = {
      id: id
    }

    // if (amount === 1) {
    //   setIsClicked(false)                        // убирает счетчик, когда он уменьшается до нуля
    // }
    dispatch(decreasingIngredients(payload))
  }

                                            // Выбор начинки + показ и скрытие счетчика
  const [isClicked, setIsClicked] = useState(false)

  const clicking = () => {
    setIsClicked(!isClicked)

    const payload = {
      id: id
    }
    dispatch(choosingIngredients(payload))
  }
                                              //



  const ingredients = useSelector(ingredientsSelector)
  const [amount, setAmount] = useState(0)


  useEffect(() => {
    const ingredient = ingredients.find(i => i.id === id)

    if (ingredient === undefined) {                         // убирает счетчик, когда он уменьшается до нуля
      setIsClicked(false)
      return
    }

    setAmount(ingredient.count)

  }, [ingredients])



  return (
    <div className={'ingredients-item'} >
        <div onClick={clicking}>
            <div className={'ingredients-name'}>{name}</div>
            <div>{price} ₽ | {mass} гр.</div>
        </div>
        {(isClicked) ?
          (<div>
              <button onClick={() => decreasing(id)}>-</button>
              {amount}
              <button onClick={() => increasing(id)}>+</button>
          </div>) : null
        }

    </div>
  )
}