import React from "react";
import './ingredients.css';
import {IngredientsItem} from "./ingredients-item";
import {IngredientsList} from "../wok-data";
import {useSelector} from "react-redux";
import { ingredientsDescriptionSelector} from "../../redux/slices/noodle-slice";


export const Ingredients = ({}) => {

    const ingredients = useSelector(ingredientsDescriptionSelector)


    return (
        <div className={'ingredients'}>
            <h1>Выберете начинку</h1>
            <div className={'ingredients-body'} >
                {
                    IngredientsList.map(i => (
                      <div >
                          <IngredientsItem
                            id={i.id}
                            name={i.name}
                            price={i.price}
                            mass={i.mass}
                          />
                      </div>))
                }
            </div>
            <div className={'topping-description'}>
                {ingredients}
            </div>
        </div>
    )
}