import React from "react";
import './noodle.css';
import {useDispatch, useSelector} from "react-redux";
import {NoodleList} from "../wok-data";
import {NoodleItem} from "./noodle-item";
import {
  choosingNoodle,
  noodleDescriptionSelector
} from "../../redux/slices/noodle-slice";


export const Noodle = ({}) => {

    const setDescription = useSelector(noodleDescriptionSelector)

    const dispatch = useDispatch()


    const onChoosingNoodle = (id) => {
      const payload = {
        id: id
      }
      dispatch(choosingNoodle(payload))
    }

    return (
      <div className={'noodle'}>
          <h1>ВЫберете основу</h1>

          <div className={'noodles'}>
              {
                  NoodleList.map(i => (
                    <div
                      onClick={() => onChoosingNoodle(i.id)}
                    >
                        <NoodleItem
                          id={i.id}
                          name={i.name}
                          price={i.price}
                          mass={i.mass}
                        />
                    </div>
                  ))
              }
          </div>
          <div className={'description'}>
            {setDescription}
          </div>
      </div>
    )
}