import {createSlice} from "@reduxjs/toolkit";
import {IngredientsList, NoodleList, SauceList} from "../../components/wok-data";


export const noodleSlice = createSlice({
  name: 'noodle-slice',
  initialState: {
    menu: {
      noodle: NoodleList,
      noodleDescription: '',

      ingredients: IngredientsList,
      ingredientsDescription: '',
      sauce: SauceList
    },

    order: {
      theNoodle: null,

      theIngredients: [],

      theSauce: [],

      finalPrice: null
    }



  },
  reducers: {
                                          //  лапша
    choosingNoodle: (state, action) => {
      const id = action.payload.id
      const noodle = state.menu.noodle.find(i => i.id === id)

      const orderNoodle = {
        id: noodle.id,
        name: noodle.name,
        price: noodle.price,
      }

      state.order.theNoodle = orderNoodle                // Выбор лапши

      state.menu.noodleDescription = noodle.description     //установка дескрипшн

    },

    gettingNoodlePrice: (state, action) => {
      state.order.noodlePrice = action.payload.noodlePrice
    },

                                            // Начинка

    choosingIngredients: (state, action) => {
      const id = action.payload.id
      const ingredient = state.menu.ingredients.find(i => i.id === id)

      state.menu.ingredientsDescription = ingredient.description            // описание начинки

      const isHasActualIngredient = state.order.theIngredients.find(i => i.id === id) !== undefined

      if (!isHasActualIngredient) {
        const orderIngredient = {
          id: ingredient.id,
          name: ingredient.name,
          price: ingredient.price,
          count: 1
        }

        state.order.theIngredients.push(orderIngredient)
      }
    },
    increasingIngredientsCount: (state, action) => {
      const index = state.ingredients.findIndex(i => i.id === action.payload.id)
      console.log('index ' + index)
      state.ingredients[index].amount += 1
    },


    increasingIngredients: (state, action) => {
      const id = action.payload.id
      const item = state.order.theIngredients.find(i => i.id === id)

      if (item == null) {
        console.error('Error - item not found ')
        return
      }

      state.order.theIngredients.find(i => i.id === id).count++
    },

    decreasingIngredients: (state, action) => {
      const id = action.payload.id
      const item = state.order.theIngredients.find(i => i.id === id)

      if (item == null) {
        console.error('Error - item not found ')
        return
      }

      if (item.count === 1) {
        const index = state.order.theIngredients.findIndex(i => i.id === id)
        state.order.theIngredients.splice(index, 1)
        return
      }

      state.order.theIngredients.find(i => i.id === id).count--
    },

                                              // Соусы

    choosingSauce: (state, action) => {
      const id = action.payload.id
      const sauce = state.menu.sauce.find(i => i.id === id)

      const orderSauce = {
        id: sauce.id,
        name: sauce.name,
        price: sauce.price,
        count: 1
      }

      state.order.theSauce.push(orderSauce)
    },

    increasingSauce: (state, action) => {
      const id = action.payload.id
      const item = state.order.theSauce.find(i => i.id ===id)

      if (item == null) {
        console.error('Error - sauce item not found ')
        return
      }

      item.count++
    },
    decreasingSauce: (state, action) => {
      const id = action.payload.id
      const item = state.order.theSauce.find(i => i.id ===id)

      if (item == null) {
        console.error('Error - sauce item not found ')
        return
      }
      if (item.count === 1) {
        const index = state.order.theSauce.findIndex(i => i.id === id)
        state.order.theSauce.splice(index, 1)
        return
      }

      item.count--
    },


                                                  // Счет общий
    gettingFinalPrice: (state, action) => {
      state.order.finalPrice = state.order.noodlePrice + state.order.theIngredients.price * state.order.theIngredients.count
    }
  }
})



/*
* Selectors
* */
export const noodleSelector = (store) => store.noodle.order.theNoodle
export const noodleDescriptionSelector = (store) => store.noodle.menu.noodleDescription
export const gettingNoodlePriceSelector = (store) => store.noodle.order.noodlePrice


export const ingredientsSelector = (store) => store.noodle.order.theIngredients
export const ingredientsDescriptionSelector = (store) => store.noodle.menu.ingredientsDescription


export const sauceSelector = (store) => store.noodle.order.theSauce



export const billSelector = (store) => {                              // Селектор и логика для финального счета
  let bill = 0

  store.noodle.order.theIngredients.forEach(i => {
    bill += i.price * i.count
  })

  const noodle = store.noodle.order.theNoodle

  if (noodle !== null) {
    bill += store.noodle.order.theNoodle.price
  }

  store.noodle.order.theSauce.forEach(i => {
    bill += i.price * i.count
  })

  return bill
}

/*
* Actions
* */
export const {
  choosingNoodle,
  gettingNoodlePrice,
  gettingFinalPrice,
  choosingIngredients,
  increasingIngredients,
  decreasingIngredients,
  choosingSauce,
  increasingSauce,
  decreasingSauce,


} = noodleSlice.actions




/*
* Reducer
* */
export const noodleReducer = noodleSlice.reducer